<template lang="pug">
.cr-row(:class="{'cr-row-uninited': !inited}")
  .cr-row-inner(ref="inner")
    slot
    .clearfix
</template>

<script>
import * as hp from 'helper-js'
import * as vf from 'vue-functions'

// find last col, row
const update = function() {
  if (!this.mounted) {
    return
  }
  const rowWidth = this.width + this.gutterX
  const rows = []
  let currentRow
  let raw // row accumulate width; row累加宽度
  if (!this.$refs.inner) {
    // prevent error when hot reload npm run dev
    return
  }
  let growExisted // row只允许一个grow col
  for (const el of this.$refs.inner.children) {
    if (el.tagName === 'BR') {
      currentRow = null
      continue
    }
    if (hp.hasClass(el, 'clearfix')) {
      continue
    }
    const id = el.getAttribute('data-vm-id')
    const col = this.colsMapping[id]
    col.isFirstCol = false
    col.isLastCol = false
    col.isLastRow = false
    if (vf.isPropTrue(col.grow)) {
      if (growExisted) {
        currentRow = null
        growExisted = false
      } else {
        growExisted = true
      }
    }
    //
    if (!currentRow) {
      currentRow = []
      rows.push(currentRow)
      raw = 0
    }
    //
    let cw // col width. _realWidth. contain margin
    const setCw = (col, w) => {
      let isPx
      if (hp.isString(w)) {
        if (w.endsWith('px')) {
          isPx = true
        }
        w = parseFloat(w)
      }
      if (!isPx && w <= 1) {
        // width is proportion; 小于等于1的是比例
        cw = parseInt(rowWidth * w)
        cw = cw > rowWidth ? rowWidth : cw // col max width is 100%
        col.cssWidth = cw - this.gutterX
      } else {
        // width is px; 指定像素
        cw = w + this.gutterX
        cw = cw > rowWidth ? rowWidth : cw // col max width is 100%
        col.cssWidth = w
      }
    }
    const restW = rowWidth - raw
    const cpw = this.getColCurrentPropWidth(col, rowWidth, restW) // col prop width for current screen size
    setCw(col, cpw)
    //
    col._realWidth = cw
    raw += cw
    if (raw > rowWidth) {
      currentRow = [col]
      rows.push(currentRow)
      raw = cw
    } else {
      currentRow.push(col)
    }
  }
  if (rows.length === 0) {
    return
  }
  // when screen narrower than first col, first row is empty, so remove it
  if (rows[0].length === 0) {
    rows.shift()
  }
  // grow col
  for (const row of rows) {
    row[0].isFirstCol = true
    hp.arrayLast(row).isLastCol = true
    // sort
    let restW = rowWidth // rest width; 递减后剩余宽度
    let growCol
    row.forEach((col, i) => {
      if (vf.isPropTrue(col.grow)) {
        growCol = col
      }
      restW -= col._realWidth
    })
    if (growCol) {
      growCol.cssWidth += restW
      growCol._realWidth += restW
    } else if (restW <= 3) {
      // 当剩下3px以内的剩余时, 加到最后一列上
      const lastCol = hp.arrayLast(row)
      lastCol.cssWidth += restW
      lastCol._realWidth += restW
    }
  }
  hp.arrayLast(rows).forEach(col => {
    col.isLastRow = true
  })
  this.$emit('updated', this)
  // recurse children row to updateWidth
  const recurse = (cpt) => {
    for (const child of cpt.$children) {
      if (child.$options.isColRow_row) {
        child.updateWidth()
      } else {
        recurse(child)
      }
    }
  }
  this.$nextTick(() => {
    recurse(this)
  })
}
const updateDebounced = hp.debounce(update)

export default {
  isColRow_row: true,
  props: {
    gutter: {default: 16, type: [Number, Array]},
  },
  // components: {},
  data() {
    return {
      mounted: false,
      width: null,
      gutterX: null,
      gutterY: null,
      colsMapping: {},
      inited: false,
    }
  },
  computed: {
    colStyle() {
      return {
        marginRight: this.gutterX + 'px',
        marginBottom: this.gutterY + 'px',
      }
    },
  },
  // watch: {},
  methods: {
    updateWidth() {
      this.width = this.$el.offsetWidth
    },
    updateGutter() {
      const {gutter} = this
      let t = hp.isArray(gutter) ? gutter : [gutter, gutter]
      this.gutterX = t[0]
      this.gutterY = t[1]
    },
    getColWidthProp(col) {
      if (col.width === 'auto') {
        if (vf.isPropTrue(col.grow)) {
          return '1px'
        } else {
          return 1
        }
      } else {
        return col.width
      }
    },
    // get responsive width
    getColCurrentPropWidth(col, rowWidth, restW) {
      const w = window.innerWidth
      const cw = this.getColWidthProp(col)
      let r
      if (w < 768) {
        r = getPrioritized(col.xs, cw)
      } else if (w < 992) {
        r = getPrioritized(col.sm, cw)
      } else if (w < 1200) {
        r = getPrioritized(col.md, col.sm, cw)
      } else {
        r = getPrioritized(col.lg, col.md, col.sm, cw)
      }
      if (hp.isFunction(r)) {
        return r(rowWidth, restW)
      }
      return r
    },
    // find last col, row
    update,
    updateDebounced,
    registerCol(colVm) {
      this.colsMapping[colVm._uid] = colVm
      this.updateDebounced()
    },
    unregisterCol(colVm) {
      delete this.colsMapping[colVm._uid]
      this.updateDebounced()
    },
  },
  // created() {},
  mounted() {
    this.mounted = true
    this.updateWidth()
    this.$watch('gutter', this.updateGutter, {deep: true, immediate: true})
    this.$watch('gutterX', this.updateDebounced)
    this.$watch('gutterY', this.updateDebounced)
    this.$watch('width', this.updateDebounced)
    //
    this.onresize = e => {
      this.updateWidth(e)
    }
    hp.onDOM(window, 'resize', this.onresize)
    //
    this.update()
    //  may get wrong width because of scroll bar
    //  可能因为滚动条后出现而得到错误的宽度
    setTimeout(() => {
      this.updateWidth()
      setTimeout(() => {
        this.inited = true
      }, 16)
    }, 0)
  },
  beforeDestroy() {
    if (this.onresize) {
      hp.offDOM(window, 'resize', this.onresize)
    }
  },
}

function getPrioritized(...arr) {
  for (const item of arr) {
    if (item != null) {
      return item
    }
  }
  return hp.arrayLast(arr)
}
</script>

<style>
.cr-row{
}
.cr-row-uninited{
  visibility: hidden;
}
.cr-row-inner > br{
  display: none;
}
.clearfix{
  clear: both;
}
</style>
